.step-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  .step {
    box-shadow: 0px 10px 0px 0px #6c6c6c;
    position: relative;
    min-height: 40vh;
    min-width: 40vh;
    background: #FFFFFF;
    border-radius: 15px;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: all 100ms ease-in 0s;
    cursor: pointer;
    &:not(.active) {
      &:active {
        transform: translateY(10px);
        box-shadow: 0px 0px 0px 0px #cc9d0e;
      }
    }
    &.active {
      box-shadow: 0px 10px 0px 0px #cc9d0e;
      background: #ffca25;
    }
    .instruction {
      position: absolute;
      bottom: 20px;
      left: 0;
      width: 100%;
      text-align: center;
      font-size: 1.5em;
    }
    h1 {
      margin: 40px 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      text-align: center;
    }
    .timer {
      font-size: 3.5em;
    }
  }
  .arrows {
    color: white;
    margin: 5vh 0;
    display: flex;
    justify-content: center;
    .arrow {
      margin: 0 20px;
    }
  }
}