@import url(https://fonts.googleapis.com/css2?family=DM+Mono:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {

}

.nav-menu{position:fixed;top:0;left:0;width:100%;padding:20px 0}.nav-menu .nav-items{color:white;width:100%;display:flex;justify-content:space-evenly}.nav-menu .nav-items a{font-size:1.5em;padding:5px 20px;border-radius:5px}.nav-menu .nav-items a.active{background:white;color:black}

.step-wrapper{display:flex;justify-content:center;align-items:center;flex-direction:column;height:100vh}.step-wrapper .step{box-shadow:0px 10px 0px 0px #6c6c6c;position:relative;min-height:40vh;min-width:40vh;background:#FFFFFF;border-radius:15px;padding:30px;display:flex;justify-content:center;align-items:center;flex-direction:column;transition:all 100ms ease-in 0s;cursor:pointer}.step-wrapper .step:not(.active):active{transform:translateY(10px);box-shadow:0px 0px 0px 0px #cc9d0e}.step-wrapper .step.active{box-shadow:0px 10px 0px 0px #cc9d0e;background:#ffca25}.step-wrapper .step .instruction{position:absolute;bottom:20px;left:0;width:100%;text-align:center;font-size:1.5em}.step-wrapper .step h1{margin:40px 0;position:absolute;top:0;left:0;width:100%;text-align:center}.step-wrapper .step .timer{font-size:3.5em}.step-wrapper .arrows{color:white;margin:5vh 0;display:flex;justify-content:center}.step-wrapper .arrows .arrow{margin:0 20px}

.routes .page-enter{opacity:0;transform:translate(0, 30px)}.routes .page-enter-active{opacity:1;transform:translate(0, 0);transition:all 300ms, transform 300ms}.routes .page-exit{opacity:1;transform:translate(0, 0)}.routes .page-exit-active{opacity:0;transform:translate(0, 30px);transition:all 300ms, transform 300ms}

.home-page{width:100%;height:80vh;display:flex;justify-content:center;align-items:center}.home-page h1{color:black;text-align:center;background:white;border-radius:5px;padding:5px 20px}

html,body,#root{width:100%;height:100%}.App{font-family:'DM Mono', monospace;background:linear-gradient(180deg, #2b2b2b 0%, #000 50%);width:100%;height:100%;overflow:hidden}a{color:inherit;text-decoration:none}

