.nav-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  .nav-items {
    color: white;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    a {
      font-size: 1.5em;
      padding: 5px 20px;
      border-radius: 5px;
      &.active {
        background: white;
        color: black;

      }
    }
  }
}